*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  max-width: 100%;
  height: auto;
  display: block;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.button {
  padding: 17px;
  width: 100%;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  font-weight: 700;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.button--light {
  color: #ffffff;
  box-shadow: 0 0 0 4px currentColor inset;
}
@media (hover: hover) {
  .button--light:hover:not(.focus-visible) {
    box-shadow: 0 0 0 4px #e8e8e8 inset;
  }
}
.button--light.focus-visible:focus {
  box-shadow: 0 0 0 4px #e8e8e8 inset;
}
.button--dark {
  color: #102654;
  box-shadow: 0 0 0 2px currentColor inset;
}
@media (hover: hover) {
  .button--dark:hover:not(.focus-visible) {
    color: #ffffff;
    box-shadow: none;
    background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  }
}
.button--dark.focus-visible:focus {
  color: #ffffff;
  box-shadow: none;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
}

.contacts {
  padding: 57px 0 64px;
  background-color: #ffffff;
}
@media (max-width: 1199px) {
  .contacts {
    padding: 62px 45px 63px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding: 64px 15px 63px;
  }
}

@media (max-width: 1199px) {
  .contacts__wrapper {
    margin: 0 auto;
    max-width: 456px;
  }
}

.contacts h2 {
  margin: 0;
  margin-bottom: 37px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .contacts h2 {
    margin-bottom: 33px;
    font-size: 42px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .contacts h2 {
    margin-bottom: 35px;
    font-size: 32px;
    line-height: 38px;
  }
}

.contacts__content {
  margin-left: 4px;
  display: grid;
  grid-template-columns: 170px 110px;
  gap: 120px;
  position: relative;
}
.contacts__content::after {
  width: 1px;
  height: 90%;
  position: absolute;
  content: "";
  top: 5%;
  left: 230px;
  background-color: #74819c;
}
@media (max-width: 1199px) {
  .contacts__content::after {
    width: 1px;
    height: 90%;
    position: absolute;
    content: "";
    top: 5%;
    left: 58%;
    background-color: #74819c;
  }
}
@media (max-width: 767px) {
  .contacts__content::after {
    display: none;
  }
}
@media (max-width: 1199px) {
  .contacts__content {
    max-width: 100%;
    margin-left: 0;
    grid-template-columns: 218px 142px;
    justify-content: space-between;
    gap: 50px;
  }
}
@media (max-width: 767px) {
  .contacts__content {
    display: block;
  }
}

@media (max-width: 767px) {
  .contacts__column:not(:last-child) {
    margin-bottom: 20px;
  }
}

.contacts__column div:not(:last-child) {
  margin-bottom: 18px;
}
@media (max-width: 1199px) {
  .contacts__column div:not(:last-child) {
    margin-bottom: 19px;
  }
}

.contacts__column h3 {
  margin: 0;
  margin-bottom: 3px;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
}
@media (max-width: 1199px) {
  .contacts__column h3 {
    margin-bottom: 5px;
    font-size: 20px;
  }
}

.contacts__column p {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
@media (max-width: 1199px) {
  .contacts__column p {
    font-size: 18px;
    line-height: 152.34%;
  }
}

.contacts__column a {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
@media (max-width: 1199px) {
  .contacts__column a {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 152.34%;
  }
}

.free-training {
