@media (max-width: 1199px) {
  .tabs__controls {
    margin-bottom: 44px;
    max-width: 332px;
    gap: 54px;
  }
}
@media (max-width: 767px) {
  .tabs__controls {
    margin-bottom: 35px;
    max-width: 100%;
    gap: 46px;
  }
}

.tabs__control {
  padding: 13px 0;
  min-width: auto;
  color: rgba(16, 38, 84, 0.5);
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.tabs__control.is-active {
  color: #102654;
  box-shadow: 0 -2px 0 0 #d10a42 inset;
  pointer-events: none;
}
@media (max-width: 1199px) {
  .tabs__control {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .tabs__control {
    font-size: 16px;
  }
}
@media (hover: hover) {
  .tabs__control:hover:not(.focus-visible) {
    text-shadow: #74819c 1px 0 10px;
  }
}
.tabs__control.focus-visible:focus {
  text-shadow: #74819c 1px 0 10px;
}

.tabs__content {
  transition: height 0.3s;
}

.tabs__element {
  width: 100%;
}

.tabs--unset .tabs__content {
  height: 100px;
}

.tabs--unset .tabs__element {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.trainers {
  padding: 50px 0 91px;
}
@media (max-width: 1199px) {
  .trainers {
    padding: 50px 0 100px;
  }
}
@media (max-width: 767px) {
  .trainers {
    padding: 34px 0 70px;
  }
}

.trainers h2 {
  margin: 0;
  margin-bottom: 48px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .trainers h2 {
    margin-bottom: 31px;
    font-size: 42px;
    line-height: 50px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .trainers h2 {
    margin-bottom: 42px;
    font-size: 32px;
    line-height: 38px;
  }
}

.trainers__container {
  padding: 0 60px;
  min-width: 0;
  max-width: 1280px;
  position: relative;
}

.trainers__swiper-wrapper {
  position: relative;
}

.trainers__swiper-wrapper .swiper-button-prev {
  width: auto;
  height: auto;
  position: absolute;
  top: 50%;
  left: -50px;
  z-index: 30;
  cursor: pointer;
}

.trainers__swiper-wrapper .swiper-button-next {
  width: auto;
  height: auto;
  position: absolute;
  top: 50%;
  right: -50px;
  z-index: 30;
  cursor: pointer;
}